.bg-light-green1 {   
    background: rgb(255,220,146);
background: linear-gradient(0deg, rgba(255,220,146,1) 65%, rgba(86,232,158,1) 93%);
    display: inline-block;
        margin: 2%;
    text-align: center;
    border-radius: 3%;
    height: 35vh;
    color: blck;

}
.bg-light-green1:hover{
    transform: scale(105%,105%);
}
.bg-light-green1 img{
    height: 200px;
    width: 200px;
}
.bg-light-green1 div h2{
    margin: 5px;
    padding: 0; 
}